@import '../../../styles/App.scss';

.aboutOurTeam {
  display: flex;
  flex-direction: column;
  align-items: center;

  background-color: #ffffff;
}

.wrapperBlok {
  display: flex;
  gap: 50px;
}

.title {
  margin: 100px 0 0 0;

  font-weight: 700;
  font-size: 48px;

  color: #000000;
}

.employee {
  margin: 30px 0 80px 0px;

  display: flex;
  flex-direction: column;
  align-items: flex-start;

  color: #000000;

  &__name {
    font-weight: 700;
    font-size: 30px;
  }

  &__post {
    margin: 10px 0 0 0;

    font-weight: 700;
    font-size: 20px;
  }

  &__desc {
    margin: 50px 0 0 0;
    max-width: 630px;

    line-height: 170%;
  }

  &__moreDetailed {
    margin: 80px 0 0 0;
    width: 280px;
    height: 60px;

    gap: 10px;
    text-transform: uppercase;

    box-shadow: 0px 4px 76px rgba(211, 46, 135, 0.3);
  }
}

.sliderTeam {
  position: relative;

  display: flex;
  align-items: center;
  justify-content: center;

  max-width: 430px;
}

.castomBtnCarusel {
  position: absolute;
  width: 460px;

  display: flex;
  justify-content: space-between;

  &__btnCarusel {
    width: 74px;
    height: 74px;
  
    display: flex;
    justify-content: center;
    align-items: center;
  
    border: none;
    border-radius: 50%;
  
    background-color: #ffffff;
    box-shadow: 0px 4px 76px rgba(211, 46, 135, 0.3);
  
    cursor: pointer;
  }
}


